<script>
    import ButtonContent from './ButtonContent.svelte'
    import {createEventDispatcher} from 'svelte';
    import Select from 'svelte-select';
    import dagre from 'cytoscape-dagre';
    import Autocomplete from '@smui-extra/autocomplete';
    import Button, { Label } from '@smui/button';
    import Modal from 'svelte-simple-modal';
    import { v4 as uuidv4 } from 'uuid';
    import AutoComplete from "simple-svelte-autocomplete"
 
    export let allGebiete = []
    let selectedNode
    let collectionCompoundNodes;
    let urlEdges = 'http://localhost:5001/api/fullEdgeList';
    let urlNodes = 'http://localhost:5001/api/fullList'
    
    
        let items = [
            { value: 'Ebene', label: 'Ebene' },
            { value: 'Region', label: 'Region' }
        ];

    let valueSelected= { value: 'Region', label: 'Region' };

	let show=false;

    let ebenenChecks = [
		'Bund',
		'Land',
		'Kommune'
	];

    let checkedEbenen = [
        
    ];

    const dispatch = createEventDispatcher();
    let cy;
    let knoten;
    let allEdges=[]
    let allNodes = []

    Promise.all([
        fetch(urlEdges)
    ]).then(([edges])=> edges.json()
    ).then(data => {
     allEdges=data
    })
    // $(function() {  
    fetch(urlNodes)
    .then(res => res.json())
    .then(data => {
     //console.log(data) 
     allNodes = data
     console.log(allNodes[0]['data'])
    // edgesArr = await fetch(urlEdges);      
                
               
                cytoscape.use(dagre);
                cy = cytoscape({
                container: document.getElementById('cy'),
                style: [
                    
                    // {
                    //     selector: 'node.highlighted',
                    //     css: {
                    //         'active-bg-color': 'red',
                    //         //'border-width': '2px'
                    //     }
                    //  },
                    {
                        selector: 'node',
                        css: {
                            shape: 'rectangle',
                            width: 70,
                            height: 50,
                            'background-color':'#61bffc',
                            // content: 'data(kuerzel)',
                            // 'text-wrap': 'wrap',
                            // 'text-valign': 'center',
                            
                        }
                        
                    },
                    {
                    selector: ':parent',
                        css: {
                            'text-valign': 'top',
                            'text-halign': 'right',
                            'background-color': 'white',
                            'border-color': 'black',
                            //'background-image': 'data:image/png;base64,',
                            'background-height': '80%',
                            'background-width' : '60%',
                            'background-image-opacity': 0.5,
                            'min-width': 1300,
